import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { TTFLoader } from 'three/examples/jsm/loaders/TTFLoader.js'
import { Font } from 'three/examples/jsm/loaders/FontLoader.js'
import { TextGeometry } from 'three/examples/jsm/Addons.js'
import { LineMaterial } from 'three/examples/jsm/lines/LineMaterial.js'
import { LineGeometry } from 'three/examples/jsm/lines/LineGeometry.js'
import { Line2 } from 'three/examples/jsm/lines/Line2.js'
import { EffectComposer } from 'three/examples/jsm/postprocessing/EffectComposer.js'
import { RenderPass } from 'three/examples/jsm/postprocessing/RenderPass.js'
import { UnrealBloomPass } from 'three/examples/jsm/postprocessing/UnrealBloomPass.js'

const w = window.innerWidth
const h = window.innerHeight
const scene = new THREE.Scene()
const camera = new THREE.PerspectiveCamera(75, w / h, 0.1, 1000)
camera.position.z = 5
const canvas = document.getElementById('canvas-webgl')
const renderer = new THREE.WebGLRenderer({ antialias: true, canvas: canvas })
renderer.setSize(w, h)
document.body.appendChild(renderer.domElement)

const ctrls = new OrbitControls(camera, renderer.domElement)
ctrls.enableDamping = true

const hemiLight = new THREE.HemisphereLight(0xffffbb, 0x008820, 1)
scene.add(hemiLight)

/* const axisHelper = new THREE.AxesHelper()
scene.add(axisHelper) */

let tX = 31

let strokeGroup
let textMesh
let composer
let bloomStrength = 0
const loader = new TTFLoader()
loader.load('./fonts/Oxanium-ExtraBold.ttf', (res) => {
    console.log(res)
    const font = new Font(res);
    const props = {
        font,
        size: 1,
        depth: 0.1,
        curveSegments: 6,
        bevelEnabled: true,
        bevelThickness: 0.08,
        bevelSize: 0.01,
        bevelOffset: 0,
        bevelSegments: 2,
    }
    const textGeo = new TextGeometry('Consult', props)
    textGeo.computeBoundingBox()
    const centerOffset = -0.5 * (
        textGeo.boundingBox.max.x - textGeo.boundingBox.min.x
    )
    textGeo.center()
    const mat = new THREE.MeshPhysicalMaterial({
        roughness: 0.5,
        transmission: 1.0,
        transparent: true,
        thickness: 1.0
    })
    textMesh = new THREE.Mesh(textGeo, mat)
    //textMesh.position.x = centerOffset
    //scene.add(textMesh)

    strokeGroup = new THREE.Group()
    strokeGroup.add(textMesh)
    //textMesh.position.z = -0.2
    //strokeGroup.position.x = centerOffset
    strokeGroup.position.z = 0.2
    strokeGroup.userData.update = (t) => {
        strokeGroup.children.forEach((child) => {
            child.userData.update?.(t)
        })
    }
    const lineMaterial = new LineMaterial({
        linewidth: 3,
        dashed: true,
        dashSize: 0.1,
        gapSize: 0.1,
        dashOffset: 0.0
    })
    let points3d2 = []
    /* const shapes = font.generateShapes('Consult', 1)
    let lineGeo = new LineGeometry()
    shapes.forEach((s2) => {
        let points2 = s2.getPoints()
        points2.forEach((p2) => {
            points3d2.push(p2.x, p2.y, 0)
        })


        console.log(lineGeo)
    })
    lineGeo.setPositions(points3d2)
    lineGeo.center()
    const strokeMesh = new Line2(lineGeo, lineMaterial)
    strokeMesh.computeLineDistances()
    scene.add(strokeMesh) */
    const shapes = font.generateShapes('Consult', 1)
    shapes.forEach((s) => {
        let points = s.getPoints()
        let points3d = []
        points.forEach((p) => {
            points3d.push(p.x - 2.6, p.y - 0.5, 0.15)
        })
        const lineGeo = new LineGeometry()
        lineGeo.setPositions(points3d)
        const strokeMesh = new Line2(lineGeo, lineMaterial)
        strokeMesh.computeLineDistances()
        let totalDist = s.getLength()
        lineMaterial.dashSize = totalDist * 1.5
        lineMaterial.gapSize = totalDist * 1.5
        lineMaterial.dashOffset = 0.0
        strokeMesh.userData.update = (t) => {
            if (t > 64) {
                t = 64
            }
            lineMaterial.dashOffset = t * 0.15
        }
        strokeGroup.add(strokeMesh)

        if (s.holes?.length > 0) {
            s.holes.forEach((h) => {
                let points = h.getPoints()
                let points3d = []
                points.forEach((p) => {
                    points3d.push(p.x - 2.6, p.y - 0.5, 0.15)
                })
                const lineGeo = new LineGeometry()
                lineGeo.setPositions(points3d)
                const strokeMesh = new Line2(lineGeo, lineMaterial)

                strokeMesh.computeLineDistances()
                strokeGroup.add(strokeMesh)
            })
        }
    })
    scene.add(strokeGroup)
    animate()
})
let timeCounter = 0
let bloomPass
let tick1 = true
let tick2 = true
let tick3 = true
let tick4 = true
let tick5 = true
function animate(t = 0) {
    requestAnimationFrame(animate)
    strokeGroup.userData.update((t * 0.002) + tX)
    timeCounter += 0.01

    if (timeCounter > 12 && tick1) {
        strokeGroup.rotation.y += 0.05
        if (strokeGroup.rotation.y > Math.PI / 4) {
            strokeGroup.rotation.y = Math.PI / 4
            tick1 = false
        }
    }
    if (timeCounter > 13 && tick2) {
        strokeGroup.rotation.y -= 0.05
        if (strokeGroup.rotation.y < - Math.PI / 4) {
            strokeGroup.rotation.y = - Math.PI / 4
            tick2 = false
        }
    }
    if (timeCounter > 14 && tick3) {
        strokeGroup.rotation.y += 0.01
        if (strokeGroup.rotation.y > - 0.3) {
            strokeGroup.rotation.y = - 0.3
            tick3 = false
        }
    }
    if (!tick3 && tick4) {
        strokeGroup.rotation.y += 0.05
        if (strokeGroup.rotation.y > 0) {
            strokeGroup.rotation.y = 0
            tick4 = false
        }
    }
    if (timeCounter > 16) {
        bloomStrength += 1
        if (bloomStrength > 100) {
            bloomStrength = 100
        }
        bloomEffect(bloomStrength)
        camera.position.z -= 0.05
        if (camera.position.z < 0.6) {
            camera.position.z = 0.6
            camera.rotation.x = -0.5
        }
        composer.render(scene, camera)
    }
    else {
        renderer.render(scene, camera)
    }
    if (timeCounter > 17) {
        timeCounter = 17
    }
    ctrls.update()
}


function handleWindowResize() {
    camera.aspect = window.innerWidth / window.innerHeight
    camera.updateProjectionMatrix()
    renderer.setSize(window.innerWidth, window.innerHeight)
}
window.addEventListener('resize', handleWindowResize, false)

function bloomEffect(bloomStrength) {
    if (tick5) {
        const renderScene = new RenderPass(scene, camera);
        bloomPass = new UnrealBloomPass(
            new THREE.Vector2(window.innerWidth, window.innerHeight),
            0,  // styrke
            0,  // radius
            0  // tærskel
        );

        composer = new EffectComposer(renderer);
        composer.addPass(renderScene);
        composer.addPass(bloomPass);
        tick5 = false
    }
    bloomPass.strength = bloomStrength
}